<!--  居民信息详情 -->
<template>
  <div class="table-detail-box resident-information-detail">
    <!-- 标题 -->
    <div class="title-box">
      <Breadcrumb>
        <span slot="custom">{{ archivesInfo.userName }}</span>
      </Breadcrumb>
    </div>
    <!-- 表格 -->
    <div class="inner-box">
      <table class="default-table resident-table" cellspacing="0" cellspadding="0">
        <tr>
          <th>姓名</th>
          <td>{{ archivesInfo.userName || EMPTYTEXT }}</td>
          <th>性别</th>
          <td>{{ archivesInfo.sexTypeStr || EMPTYTEXT }}</td>
        </tr>
        <tr>
          <th>年龄</th>
          <td>{{ archivesInfo !== null ? archivesInfo.age + '岁' : EMPTYTEXT }}</td>
          <th>电话</th>
          <td>{{ archivesInfo.mobile || EMPTYTEXT }}</td>
        </tr>
        <tr></tr>
        <tr>
          <th>身份证号</th>
          <td>{{ archivesInfo.idNo || EMPTYTEXT }}</td>
          <th>出身日期</th>
          <td>{{ archivesInfo.birthdayStr || EMPTYTEXT }}</td>
        </tr>
        <tr>
          <th>医保卡</th>
          <td>{{ archivesInfo.medicareCard || EMPTYTEXT }}</td>
          <th>过敏史</th>
          <td>{{ archivesInfo.drugAllergyCase || EMPTYTEXT }}</td>
        </tr>
        <tr>
          <th>常住类型</th>
          <td>
            {{
              archivesInfo.liveType === 1
                ? '户籍'
                : archivesInfo.liveType === 2
                ? '非户籍'
                : EMPTYTEXT
            }}
          </td>
          <th>现住址</th>
          <td>
            {{
              archivesInfo.liveProvinceName +
                archivesInfo.liveCityName +
                archivesInfo.liveCountyName +
                archivesInfo.liveTownName +
                archivesInfo.liveVillageName +
                archivesInfo.liveAddress || EMPTYTEXT
            }}
          </td>
        </tr>
        <tr>
          <th>户籍地址</th>
          <td>
            {{
              archivesInfo.residenceProvinceName +
                archivesInfo.residenceCityName +
                archivesInfo.residenceCountyName +
                archivesInfo.residenceTownName +
                archivesInfo.residenceVillageName +
                archivesInfo.residenceAddress || EMPTYTEXT
            }}
          </td>
          <th>居民类型</th>
          <td>{{ archivesInfo.personTypeStr || EMPTYTEXT }}</td>
        </tr>
        <tr>
          <th>紧急联系人</th>
          <td>{{ archivesInfo.urgencyContactName || EMPTYTEXT }}</td>
          <th>紧急联系人电话</th>
          <td>{{ archivesInfo.urgencyContactPhone || EMPTYTEXT }}</td>
        </tr>
        <tr>
          <th>标签</th>
          <td colspan="3">{{ archivesInfo.labelNameList || EMPTYTEXT }}</td>
        </tr>
        <tr>
          <th>家庭成员</th>
          <td colspan="3">
            <div class="tstd">
              <div class="leftbox" v-if="archivesInfo.headStatus === 1">
                户主：{{ archivesInfo.userName || EMPTYTEXT }}
              </div>
              <div class="rightbox">
                成员：
                <template
                  v-if="
                    archivesInfo && archivesInfo.familyList && archivesInfo.familyList.length > 0
                  "
                >
                  <span v-for="(item, index) in archivesInfo.familyList" :key="index">
                    {{ item.userName + '（' + item.personRelation + '）' }}
                    {{ index !== archivesInfo.familyList.length - 1 ? '，' : '' }}
                  </span>
                </template>
                <template v-else
                  >无</template
                >
              </div>
            </div>
          </td>
        </tr>
      </table>
    </div>
  </div>
</template>

<script>
export default {
  name: 'resident-information-detail',
  data() {
    return {
      archivesInfo: {} //居民信息
    }
  },
  created() {
    this.getDetailInfo() //获取居民信息详情
  },
  methods: {
    //获取居民信息详情
    getDetailInfo() {
      this.$emit('viewLoad', true)
      this.$http
        .get(
          process.env.VUE_APP_API +
            '/family/person/getDtoByRecordId?recordId=' +
            this.$route.query.id
        )
        .then(res => {
          if (res.code === 200) {
            this.archivesInfo = res.data
          } else {
            this.$message.error(res.msg)
          }
          this.$emit('viewLoad', false)
        })
        .catch(error => {
          this.$emit('viewLoad', false)
          this.$message.error(error)
        })
    }
  }
}
</script>

<style lang="scss" scoped>
@import '~@/assets/styles/table-default';
.resident-information-detail {
  .resident-table {
    width: 100%;
    max-width: 1320px;
    .tstd {
      display: flex;
      .leftbox {
        width: 30%;
      }
      .rightbox {
        display: flex;
        flex: 1;
        flex-wrap: wrap;
      }
    }
  }
}
</style>
